import React from 'react';
import {ICONS} from './icons.js'

export default function StudentInfo(props) {
  return (
    <ul id="student-info" className="nobullet">
      { props.studentInfo.map(item => {
        return (
          <li key={item.name} className="result-item">
            {/*<p className="result-item-name">{item.name}</p>*/}
            <i className={ICONS[item.icon]}></i>
            <h2 className="result-item-value">{item.value}</h2>
          </li>
        )
      })}
    </ul>
  )
}



