import React from 'react';
import {ICONS} from './icons.js'
import StatusBar from './StatusBar.js'
import Button from '@material-ui/core/Button'
import StudentFinder from './StudentFinder.js'
import StudentInfo from './StudentInfo.js'

import './css/checkIn.css'

export default class CheckIn extends React.Component {


  render() {
    const {sessionStatus, onScanStart, onCompleteSession, onCancelSession, studentInfo, isScanning} = this.props

    return (
      <div className="check-in card card-default">

        <div className="card-header">

          <div className="card-title">
            <i className={ICONS.userCheck}></i>
            Реєстрація виборця
          </div>

        </div>

        <div className="card-block">

          {sessionStatus === 100 &&
            <StudentFinder
              onScanStart={onScanStart.bind(this)}
              isScanning={isScanning}
            />
          }

          {sessionStatus === 200 &&
            <StudentInfo
              studentInfo={studentInfo}
            />
          }

          <div className="check-in-controls">
            {sessionStatus === 200 &&
              <Button onClick={onCompleteSession} variant="contained" color="primary">завершити</Button>
            }
            <Button onClick={onCancelSession} >скасувати сесію</Button>
          </div>



        </div>

      </div>
    )
  }

}

