import React from 'react'

import axios from 'axios'
import Raven from 'react-raven'
import { MuiThemeProvider } from '@material-ui/core/styles';
import Quagga from 'quagga'
import CheckIn from './CheckIn.js'
import Button from '@material-ui/core/Button';
import {ICONS} from './icons.js'
import {THEME} from './theme.js'
import logo from './img/logo.jpg'
import votingImg from './img/voting.png'
import Footer from './Footer.js'

/******

sessionStatus

100 – session just opened, choosing docType

200 – success and data for ticketId
201 –
202 – error for ticketId [already voted]
203 – error for ticketId [already in session]
204 – error for ticketId [not found]

300 – sent docType!=ticket, fullName
301 – success and data for docType!=ticket and fullName
302 - error for docType!=ticket and fullName [already voted]
303 - error for docType!=ticket and fullName [already in session]
304 - error for docType!=ticket and fullName [not found]

400 – submitted


******/

export default class extends React.Component {
  state = {
    loggedIn: false,
    sessionIsOpen: false,
    sessionStatus: null,
    isScanning: false,
    // status: {
    //   id: 'findUser',
    //   text: 'Готово до реєстрації',
    //   showIcon: false,
    //   icon: ICONS.readyIcon,
    //   isLoading: false
    // },
    studentInfo: []

  }

  componentDidMount() {
    axios.defaults.baseURL = 'https://elists-dev.herokuapp.com/api/elists'
    axios.defaults.withCredentials = true
    this.closeSessions()
    this.getAuth()
    this.onDetected()

  }

  closeSessions () {
    axios.post('/close_sessions', {})
      .catch(err => {
        this.handleError(err.error)
      })
  }

  getAuth() {
    axios.post('/refresh_auth', {})
      .then(res => {
        this.setState({
          loggedIn: true
        })
      })
      .catch(err => {
        this.setState({
          loggedIn: false
        })
        window.location.href = "https://elists-dev.herokuapp.com/admin/login?next=/elists/front"

      })
  }

  openSession () {
    let data = {};

    axios.post('/start_new_session', data)
      .then(res => {
        console.log(res)
        this.setState({
          sessionIsOpen: true,
          sessionStatus: 100,
          checkInSessionToken: res.data.data.check_in_session.token
        })
      })
      .catch(err => {
        this.handleError(err.error)
      })
  }

  searchByTicketNumber (ticketNum) {
    let data = {}
    data.student_ticket_number = ticketNum
    data.check_in_session_token = this.state.checkInSessionToken


    axios.post('/search_by_ticket_number', data)
    .then(res => {
        console.log(res)
        var currentInfo = this.state.studentInfo

        let info = [
          {name: 'ПІБ', value: res.data.data.full_name},
          {name: 'Освітній рівень', value: res.data.data.educational_degree === 1 ? 'Бакалавр' : 'Магістр'},
          {name: 'Курс', value: res.data.data.year}
        ]

        // show student info
        this.setState({
          studentInfo: info,
          sessionStatus: 200
        })
    })
    .catch(err => {
      this.handleError(err.response, 302)
    })

  }

  submitStudent () {

  }

  handleError(err, status) {
    console.error(err)
    this.setState({
      error: err,
      sessionStatus: status
    })
  }

  cancelSession () {
    let data = {}
    data.check_in_session_token = this.state.checkInSessionToken

    axios.post('/cancel_session', data)
      .then(res => {
        console.log(res)

        this.endSession()
      })
      .catch(err => {
        console.log(err)

        this.handleError(err.error)
      })

  }

  completeSession () {
    let data = {}
    data.check_in_session_token = this.state.checkInSessionToken

    axios.post('/complete_session', data)
      .then(res => {
        this.endSession()
      })
      .catch(err => {
        this.handleError(err.error)
      })
  }

  endSession () {
    this.setState({
      sessionIsOpen: false,
      sessionStatus: null,
      checkInSessionToken: null,
      studentInfo: []
    })
  }

  render() {

    return (
      <div className="page-content-wrapper ">

        <Raven dsn='https://6114eea799a146298b71db08a24d036d@sentry.io/1241630' />

        <MuiThemeProvider theme={THEME}>
            <div className="header-and-content">

              <header>
                <div className="title">ЦВК</div>
                <div className="logo">
                  <img src={logo} alt="logo" height="45" />
                </div>
                <div className="title">КНУ</div>
              </header>

              <div className="content ">

                {
                  this.state.loggedIn && !this.state.sessionIsOpen &&
                  <div className="card new-session-card">
                    <img src={votingImg} alt="" />
                    <div className="card-footer">
                      <Button variant="contained" color="primary" onClick={this.openSession.bind(this)}>почати</Button>
                    </div>
                  </div>
                }

                {
                  this.state.loggedIn && this.state.sessionIsOpen &&
                  <CheckIn
                    status={this.state.status}
                    studentInfo={this.state.studentInfo}
                    onScanStart={this.initScan.bind(this)}
                    onCancelSession={this.cancelSession.bind(this)}
                    onCompleteSession={this.completeSession.bind(this)}
                    sessionStatus={this.state.sessionStatus}
                    isScanning={this.state.isScanning}
                  />
                }

              </div>

            </div>

            <Footer />


        </MuiThemeProvider>
      </div>

    )
  }

  initScan() {
    this.setState({isLoading: true})
    Quagga.init({
        numOfWorkers: getCoresNumber(),
        inputStream : {name : "Live",type : "LiveStream",target: document.querySelector('#ticketScanner')},
        decoder : {readers : ["code_128_reader"]},
        frequency: 10
      }, (err) => {
          if (err) {
            console.log(err)
            return
          }

          Quagga.start()

          this.setState({
            isScanning: true,
            status: {
              icon: ICONS.scanningIcon,
              text: 'сканується',
            }
          })

      })


  }

  onDetected() {
    Quagga.onDetected((data) => {
        let result = data.codeResult.code
        if (result.length === 8) {
          // playSuccessSound()
          Quagga.stop()

          let info = Array.prototype.slice(this.state.studentInfo)
          let infoItem = {name: 'Номер квитка', value: result}
          info.push(infoItem)

          // show student ID
          this.setState({
            studentInfo: info,
            isScanning: false
          })

          this.searchByTicketNumber(result)

        } else {
          alert('Card has to contain 8 digits')
        }
    })
  }
}


function getCoresNumber() {
  try {
    let cores = navigator.hardwareConcurrency
    return cores
  } catch (err) {
    console.error(err)
  }
}



