import React from 'react';

import {ICONS} from './icons.js'
import './css/ticketScanner.css'

export default function TicketScanner(props) {
  return (
    <div id="ticketScanner" className={props.isScanning ? 'show' : ''}>
      {!props.isScanning &&
        <div className="iconWrapper">
          <i className={ICONS.scannedIcon}></i>
        </div>
      }
    </div>
  )
}



